.footer .content-footer{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    padding: 25px;
}
.footer .content-footer .wrap-footer {
    border-top: 3px solid #ff014f;
    padding: 30px;
  
}
.footer .content-footer  .copy{
    color: white;
    margin-top: 20px;
}
.footer .content-footer img{
    width: 100px;
}

/* responsive */
@media  (max-width: 500px) { 
    .footer .content-footer .copy{
        font-size: 12px;
    }
    .footer .wrap-footer {
        display: flex;
        justify-content: center;
        align-self: center;
    }
}