
.home .container {
  display: grid;
  grid-template-columns: 0.9fr 1fr;
  grid-gap: 30px;
  margin-top: 130px;
}
.home .slider-info{
  box-shadow: 5px 5px 16px 5px rgba(0, 0, 0, 0.42);
  padding: 50px;
}
.slider-info .title {
  color: #ffffff;
  font-weight: 700;
  font-size: 28px;
  line-height: 62px;
  margin-bottom: 11px;
}
.slider-info .title span {
  color: #ff014f;
}
.slider-info .desc{
    color: #c4cfde;
    font-size: 16px;
    margin-bottom: 30px;
}

 
.slider-info .icons{ 
  display: flex;
  flex-direction: column;
  gap: 30px;
}
.slider-info .icons .fichier img {
  margin-right: 20px;
  width: 20px;
}
.slider-info .icons .fichier{
  color: white;
  font-size: 16px;
  display: flex;
}
.slider-info .icons .message img {
  margin-right: 20px;
  width: 20px;
}
.slider-info .icons .message{
  color: white;
  font-size: 16px;
  display: flex;
}

.slider-info .icons .position img {
  margin-right: 20px;
  width: 20px;
}
.slider-info .icons .position{
  color: white;
  font-size: 16px;
  display: flex;
}

.slider-info .icons .tel img {
  margin-right: 20px;
  width: 20px;
}
.slider-info .icons .tel{
  color: white;
  font-size: 16px;
  display: flex;
}
.slider-info button {
padding: 8px 20px;
margin-top: 30px;
 margin-right: 20px;
border-radius: 6px;
background: linear-gradient(145deg, #393a3c, #23272b);
position: relative;
z-index: 2;
font-size: 14px;
font-weight: 500;
border: 0 none;
color: #ff014f;
}
.slider-info button:hover{
  background: linear-gradient(145deg, #bdc0c5, #bbc0c5);
  cursor: pointer;
}
.container .picture {
  display: flex;
  justify-content: center;
  align-items: center;
}
/* responsive */
@media  (max-width: 500px) {
  .home .container {
    display: grid;
    grid-template-columns:  1fr;
    grid-template-rows: 1fr 1fr;
    grid-gap: 30px

  }
  .slider-info .title {
    margin-top: 20px;
  }
  
  .home  {

    width: 100%;
  }
  .slider-info .title {
    color: #ffffff;
    font-weight: 700;
    font-size: 20px;
    line-height: 30px;
    margin-bottom: 11px;
  }
  .slider-info .desc{
    color: #c4cfde;
    font-size: 16px;
    margin-bottom: 30px;
}
.slider-info .icons .fichier{
  color: white;
  font-size: 14px;
}
.slider-info .icons .message{
  color: white;
  font-size: 14px;
}
.slider-info .icons .position{
  color: white;
  font-size: 14px;
}
.slider-info .icons .tel{
  color: white;
  font-size: 14px;
}
.container .picture img {
  width: 100%;
}

}
