.education .container-education {
  display: grid;
  grid-template-columns: 1fr 1fr;
}
.education .container-education .annee {
  color: #ff014f;
  font-size: 14px;
  letter-spacing: 2px;
  font-weight: 500;
  margin-top: 30px;
}
.education .container-education .education {
  font-size: 36px;
  font-weight: 700;
  line-height: 62px;
  color: #f8f9fa;
}
.education .container-education  .title {
  color: #c4cfde;
  font-size: 24px;
  line-height: 39px;
  margin-bottom: 7px;
  font-weight: 500;
}
.education .container-education  .years {
  color: #878e99;
  font-size: 14px;
  line-height: 39px;
  margin-bottom: 7px;
  font-weight: 200;
  box-sizing: border-box;
}
.education .container-education  .description {
  color: #878e99;
  font-size: 18px;
  line-height: 28px;
  margin-bottom: 0px;
  box-sizing: border-box;
}
.education .container-education .wrapper {
  border-left: 3px solid #ff014f;
  padding: 30px;
}

.education .container-education .wrapper .wrap {
  box-shadow: 1px 0px 18px 3px rgba(0, 0, 0, 0.64);
  padding: 30px;
  position: relative;
  margin-top: 20px;
}
.education .container-education .wrapper .wrap::before {
  content: "";
  width: 30px;
  height: 3px;
  background-color: #ff014f;
  position: absolute;
  left: -30px;
  top: 50px;
}
.education .container-education .wrapper .wrap:hover{
    background-color: #1B1D21;
    transition: 0.5s ease-in-out;
    
}

.education .container-education .wrapper .wrap:hover .title,
.education .container-education .wrapper .wrap:hover .years{
    color: white;   
}



/* responsive */
@media  (max-width: 500px) { 

  .education .container-education {
    display: grid;
    grid-template-columns: 1fr;
    grid-template-rows: 1fr ;
  }
  .education .container-education  .title {
    color: #c4cfde;
    font-size: 22px;
    line-height: 39px;
    margin-bottom: 7px;
    font-weight: 500;
  }

}