/* nav section */

nav {
  width: 100%;
  padding: 30px 0;
  position: fixed;
  z-index: 9;
  transition: .3s ease-in-out;
  top: 0;
  background-color: #27272E;
}

nav.fixed {
  padding: 15px 0;
  background-color: white;
  box-shadow: rgba(0, 0, 0, 0.1) 0px 4px 12px;
  opacity: 0.95;
}

nav .container {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

nav .logo {
  font-weight: 600;
  font-style: italic;
  cursor: pointer;
}

nav .logo img {
  width: 50px;
}

nav .menu {
  display: flex;
  align-items: center;
  gap: 40px;
  color: white;
  font-weight: 500;
}

nav .menu .item {
  cursor: pointer;
}

nav .menu .item button {
  border: none;
  color: white;
  font-weight: 500;
  padding: 4px 16px;
  cursor: pointer;
}
nav .menu  li{
  list-style: none;
}
nav .menu  li:hover{
  color: #ff014f;
}
nav .menu-icon {
  cursor: pointer;
  display: none;
  justify-content: center;
  align-items: center;
  z-index: 9999;
}
nav .menu-icon img{
  width: 30px;
  height: 30px;
}

@media (max-width:500px) {

  /* nav section style */


  nav .menu {
      display: flex;
      position: absolute;
      right: -100%;
      top: 0%;
      width: 70vw;
      height: 100vh;
      flex-direction: column;
      align-items: flex-start;
      gap: 25px;
      padding: 40px;
      border-top-left-radius: 5px;
      border-end-start-radius: 5px;
      transition: .3s ease-in-out;
      background-color: #191B1E;
  }

  nav.fixed .menu {
      color: white;
  }

  nav .menu.active{
      right: 0%;
  }

  nav .menu-icon {
      display: flex;
  }
}