.skills .title{
  color: #ff014f;
  letter-spacing: 1px;
  display: block;
  line-height: 30px;
  text-align: center;
  margin-top:50px;
  font-size: 40px;
  font-weight: 700;
}
.skills .skills-content {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 30px;
  margin-top: 50px;
  padding: 20px;
}
span{
  color: white;
}


.skills .skills-content .right-skills .bars {
  margin-bottom: 15px;
}

.skills .skills-content .right-skills .info {
  display: flex;
  margin-bottom: 5px;
  align-items: center;
  justify-content: space-between;
  gap: 30px;
}

.skills .skills-content .right-skills span {
  font-weight: 500;
  font-size: 18px;
}

.skills .skills-content .right-skills .line {
  height: 5px;
  width: 100%;
  background: lightgrey;
  position: relative;
}

.skills .skills-content .right-skills .line::before {
  content: "";
  position: absolute;
  height: 100%;
  left: 0;
  top: 0;
  background: #ff014f;
}

.skills-content .right-skills .html::before {
  width: 90%;
}



.skills-content .right-skills .js::before {
  width: 80%;
}

.skills-content .right-skills .react::before {
  width: 90%;
}


/*left*/



.skills .skills-content .left-skills .bars {
  margin-bottom: 15px;
}

.skills .skills-content .left-skills .info {
  display: flex;
  margin-bottom: 5px;
  align-items: center;
  justify-content: space-between;
  gap: 30px;
}

.skills .skills-content .left-skills span {
  font-weight: 500;
  font-size: 18px;
}

.skills .skills-content .left-skills .line {
  height: 5px;
  width: 100%;
  background: lightgrey;
  position: relative;
}

.skills .skills-content .left-skills .line::before {
  content: "";
  position: absolute;
  height: 100%;
  left: 0;
  top: 0;
  background: #ff014f;
}

.skills-content .left-skills .html::before {
  width: 90%;
}

.skills-content .left-skills .symfony::before {
  width: 65%;
}

.skills-content .left-skills .js::before {
  width: 70%;
}

.skills-content .left-skills .react::before {
  width: 75%;
}


/* responsive */
@media  (max-width: 500px) {
  .skills .skills-content  {
      display: grid;
      grid-template-rows: 1fr 1fr;
      grid-template-columns: 1fr;
  }
  .skills .title{
    color: #ff014f;
    letter-spacing: 1px;
    display: block;
    line-height: 50px;
    text-align: center;
    margin-top:50px;
    font-size: 40px;
    font-weight: 700;
  }
}