.blog .title {
  color: #ff014f;
  letter-spacing: 1px;
  display: block;
  line-height: 14px;
  text-align: center;
  margin-top: 50px;
}
.blog .sub-title {
  text-align: center;
  font-size: 40px;
  font-weight: 700;
  line-height: 1.2;
  color: #f8f9fa;
  margin-top: 15px;
  margin-bottom: 60px;
}

.blog .container-blog {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 20px;
}
.blog .blog-content {
  position: relative;
  box-shadow: 0px 0px 30px 9px rgba(0, 0, 0, 0.4);
  border-radius: 20px;
  padding-top: 100%;
  overflow: hidden;
}
.blog .blog-content img {
  position: absolute;
  top: 0;
  left: 0;
  border-radius: 10px;
  width: 100%;
  transition: 5s ease-in-out;
}
.blog .blog-content img:hover{
    top: 100%;
    transform: translateY(-100%);
}
.blog .blog-content .title-blog {
  color: #ff014f;
  font-weight: 700;
  font-size: 12px;
  letter-spacing: 1px;
  text-transform: uppercase;
  display: inline-block;
  position: relative;
  padding: 4px;
  margin-top: 10px;
}
.blog .blog-content .desc {
  font-weight: 600;
  font-size: 23px;
  line-height: 34px;
  display: inline-block;
  color: #f8f9fa;
}
.blog .blog-content .desc:hover {
  color: #ff014f;
  cursor: pointer;
}
.blog .blog-content:hover {
  background-color: #1b1d21;
  transition: 0.5s ease-in-out;
  cursor: pointer;
}

/* responsive */
@media (max-width: 500px) {
  .blog .sub-title {
    text-align: center;
    font-size: 22px;
    margin-bottom: 30px;
  }
  .blog .container-blog {
    display: grid;
    grid-template-columns: 1fr;
    grid-template-rows: repeat(3, 1fr);
    gap: 10px;
  }

  .blog .blog-content .desc {
    font-weight: 600;
    font-size: 20px;
    line-height: 34px;
    display: inline-block;
    color: #f8f9fa;
  }
}
