.alert{
    position: fixed;
    z-index: 99999999999;
    top: 0;
    width: 100%;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: rgba(0,0,0,0.7);

}
.alert .alert-container{
    background-color: #1B1C1E;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    border-radius: 10px;
    color: white;
    padding: 40px;
}
.alert .alert-container img{
    width: 50px;
}
.alert .alert-container button{
    margin-top: 30px ;
    padding : 6px 12px;
    background-color: #ff014f;
    border: 1px solid #ff014f;
    border-radius: 4px;
    color: white;
}