.contact .title {
    color: #ff014f;
    letter-spacing: 1px;
    display: block;
    line-height: 14px;
    text-align: center;
    margin-top:50px;
    }
.contact .sub-title {
    text-align: center;
    font-size: 50px;
    font-weight: 700;
    line-height: 1.2;
    color:#f8f9fa;
    margin-top: 15px;
    margin-bottom: 60px;
}
.contact .contact-content{
    width: 100%;
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 50px;
}
.contact .contact-content .left-contact {
    box-shadow: 5px 5px 16px 5px rgba(0, 0, 0, 0.42);
    padding: 5px;
    height: max-content;
}
.contact .contact-content .left-contact img{
    width: 100%;
    height: auto;
    border-radius: 5px;
    transition: 0.5s ease-in-out;
    
}
.contact .contact-content .left-contact img:hover{
    scale: 1.05;
    cursor: pointer;
}
.contact .contact-content .left-contact .name{
    font-size: 29px;
line-height: 44px;
color: #E4E6EA;
margin-bottom: 9px;
font-weight: 700;
}
.contact .contact-content .left-contact .descr{
font-size: 18px;
font-weight: 400;
color: #878e99;
line-height: 30px;
display: inline-block;
margin-bottom: 20px;
}
.contact .contact-content .left-contact span {
    color: #ff014f;
    font-size: 16px;
    font-weight: 500;
    margin-top: 30px;
}
.contact .contact-content .left-contact .phone{
    font-size: 16px;
    font-weight: 400;
    color: #c4cfde;
    margin-top: 15px;
}
.contact .contact-content .left-contact .email{
    font-size: 16px;
    font-weight: 400;
    color: #c4cfde;
    margin-top: 15px;
}
.contact .contact-content .left-contact .find{
    font-size: 14px;
    font-weight: 500;
    color: #878e99;
    margin-top: 25px;
    box-sizing: border-box;

}
.contact .contact-content .left-contact .social-media .fb{
    box-shadow: rgb(28, 30, 34) 10px 10px 19px 0px, rgb(38, 42, 46) -10px -10px 19px 0px;
    width: 50px;
    height: 50px;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 6px;
}
.contact .contact-content .left-contact .social-media .fb , 
.contact .contact-content .left-contact .social-media .twitter,
.contact .contact-content .left-contact .social-media .linkdin,
.contact .contact-content .left-contact .social-media .github {
    box-shadow: rgb(28, 30, 34) 10px 10px 19px 0px, rgb(38, 42, 46) -10px -10px 19px 0px;
    width: 50px;
    height: 50px;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 6px;
}

.contact .contact-content .left-contact .social-media .fb:hover,
.contact .contact-content .left-contact .social-media .twitter:hover,
.contact .contact-content .left-contact .social-media .linkdin:hover,
.contact .contact-content .left-contact .social-media .github:hover{
    background-color: #191B1E;
    cursor: pointer;
}
.contact .contact-content .left-contact .social-media{
    padding: 20px ;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    gap: 30px;
    
}
.contact .contact-content .left-contact .social-media a img {
    width: 25px;
}

.contact .contact-content .right-contact {
    box-shadow: 5px 5px 16px 5px rgba(0, 0, 0, 0.42);
    padding: 40px;
    width: 100%;
}
.contact .contact-content .right-contact p{
    font-size: 12px;
color: #a0a8b3;
font-weight: 500;
letter-spacing: 1px;
text-transform: uppercase;
margin-bottom: 14px;
text-align: left;
display: block;
}
.contact .contact-content .right-contact input{
    width: 100%;
    height: 100px;
    background-color: #191B1E;
   border: 1px solid #191B1E;
   border-radius: 6px;
}
.contact .contact-content .right-contact textarea {
    background-color: #191B1E;
   border: 1px solid #191B1E;
   border-radius: 6px;
}
.contact .contact-content .right-contact .name .input-name ,
.contact .contact-content .right-contact .phone .input-phone,
.contact .contact-content .right-contact .email .input-email
  {
    height: 40px;
    color: #E4E6EA;
}
.contact .contact-content .right-contact .subject .input-subject{
    height: 60px;
    columns:#E4E6EA ;
    color: #E4E6EA;
}
.contact .social-media img{
    display:flex ;
    justify-content: center;
    align-items: center;
    width: 20px;
}

.contact .contact-content .right-contact button{
    width: 100%;
    border: 1px solid #ff014f;
    border-radius: 5px;
    color: white;
    font-size: 16px;
    font-weight: 400;
    padding: 16px 6px;
    margin-top: 20px;
    background: #ff014f;
    background-color: #ff014f;
     background-position-x: 0%;
     background-position-y: 0%;
     background-repeat: repeat;
  background-attachment: scroll;
  background-size: auto;
}
.contact .contact-content .right-contact button:hover{
background-color: transparent;
cursor: pointer;
}

.contact .contact-content .right-contact textarea{
    width: 100%;
}
.contact .contact-content .right-contact textarea {
    color: #E4E6EA;
}

/* responsive */
@media  (max-width: 500px) {
    .contact .sub-title {
        font-size: 25px;
        margin-bottom: 30px; 
    }
    .contact .contact-content {
        display: grid;
        grid-template-columns: 1fr;
        grid-template-rows: 1fr 1fr;
        gap: 5px;
    }
    
    .contact .contact-content {
        width: 100%;
    }
    .contact .contact-content .right-contact {
        box-shadow: 5px 5px 16px 5px rgba(0, 0, 0, 0.42);
        padding: 20px;
        width: 90%;
    }

    .contact .social-media a img{

        width: 10px;
    }
    .contact .contact-content .left-contact .social-media img{
        width: 15px;
    }
    .contact .contact-content .right-contact {
        width: 90%;

    }
    .contact .contact-content .left-contact .social-media a img {
        width: 20px;
    }
 }